.navbar69 {
  height: 10%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  background-color: white;
  border-bottom: 0px solid black;
  box-shadow: 0px 15px 10px -15px #111;
  margin-bottom: 2%;
  position: -webkit-sticky;
    position: sticky;
    top: 0;
  z-index: 1000;
}

.menu-bars {
  margin-left: 2rem;
  font-size: 2rem;
  background: none;
}

.nav-menu {
  width: 250px;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: fixed;
  top: 0;
  left: -100%;
  background-color: $bg-color;
  transition: 850ms;
  z-index:2000;

  &.active {
    left: 0;
    transition: 350ms;
  }

  .nav-menu-items {
    width: 100%;
    color:white;

    .navbar-toggle {
      width: 100%;
      height: 80px;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      background-color: $bg-color;
    }

    .nav-text {
      height: 60px;
      padding: 8px 0px 8px 16px;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      list-style: none;

      a {
        height: 100%;
        width: 95%;
        padding: 0 16px;
        display: flex;
        align-items: center;
        font-size: 18px;
        color: $white;
        border-radius: 4px;
        text-decoration: none;

        &:hover {
          background-color: $hover-blue;
        }

        span {
          margin-left: 16px;
        }
      }
    }
  }
}

  

